<template>
  <v-app id="inspire">
    <v-app-bar app dense dark>
      <v-app-bar-nav-icon @click="drawer = true"></v-app-bar-nav-icon>
      <v-toolbar-title @click="menuClick('Home')">AniPlace</v-toolbar-title>
      <v-form class="form-busca">
        <v-text-field
            v-model="pesquisa"
            solo
            label="Pesquisar"
            clearable
        ></v-text-field>
        <v-btn icon>
          <v-icon>mdi-magnify</v-icon>
        </v-btn>
      </v-form>
      <div class="appbar-perfil">
        <div class="appbar-dropdown-activator" @click="appbardropdown = !appbardropdown">
          <div class="left">
            <img :src="this.$store.state.auth.user.foto!==null?'./img/users/perfil/'+this.$store.state.auth.user.foto:'./img/users/default.jpg'" alt="Imagem de usuário padrã."/>
            <span>{{this.$store.state.auth.user.nome}}</span>
          </div>
          <div class="right">
            <v-icon>mdi-chevron-down</v-icon>
          </div>
        </div>
        <v-list class="appbar-dropdown" v-show="appbardropdown">
          <v-list-item @click="menuClick('Perfil')">
            <v-list-item-title>Perfil</v-list-item-title>
          </v-list-item>
          <v-list-item @click="menuClick('config')">
            <v-list-item-title>Conta</v-list-item-title>
          </v-list-item>
          <v-list-item @click="sair()">
            <v-list-item-title>Sair <v-icon>mdi-logout</v-icon></v-list-item-title>
          </v-list-item>
        </v-list>
      </div>
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" absolute temporary dark>
      <v-btn icon class="left btn-close" @click="drawer = false">
        <v-icon>mdi-window-close</v-icon>
      </v-btn>
      <v-list nav dense>
        <v-list-item-group v-model="group" active-class="deep-purple--text text--accent-4">
          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-home</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Home</v-list-item-title>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-account</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Account</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
    <v-main>
      <v-container fill-height fluid>
        <DestaquesComponent class="slider"/>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import {mapActions} from "vuex";
import DestaquesComponent from "@/components/DestaquesComponent";

export default {
  name: "HomePage",
  components: {DestaquesComponent},
  data: () => ({
    drawer: false,
    group: null,
    appbardropdown: false,
    pesquisa: ""
  }),
  methods: {
    ...mapActions('auth', ['ActionKillSession']),
    menuClick(nomeRota){
      if(this.$route.name !== nomeRota){
        this.$router.push({name: nomeRota});
        this.appbardropdown = false;
      }else{
        this.$router.go(0);
      }
    },
    sair(){
      this.ActionKillSession();
      this.$router.replace('/login');
    },
    nav(destino, idusuario){
      this.$router.push({path: destino, params: {idusuario: idusuario}});
    }
  }
}
</script>

